<script>
export default {
  name: "RouteView",
  props: {
    keepAlive: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {};
  },
  render() {
    const {
      $route: { meta }
    } = this;
    const inKeep = (
      // <!-- 失活的组件将会被缓存！-->
      <div>
        <div class="content">
          <router-view />
        </div>
      </div>
    );
    const notKeep = (
      <div class="content">
        <router-view />
      </div>
    );
    // 这里增加了 multiTab 的判断，当开启了 multiTab 时
    // 应当全部组件皆缓存，否则会导致切换页面后页面还原成原始状态
    // 若确实不需要，可改为 return meta.keepAlive ? inKeep : notKeep
    if (meta.keepAlive === false) {
      return notKeep;
    }
    return this.keepAlive || meta.keepAlive ? inKeep : notKeep;
  }
};
</script>
